<template>
  <div class="article-page">
    <nav class="my-nav van-hairline--bottom">
      <a
        href="javascript:;"
        :class="{ active: sorter === 'weight_desc' }"
        @click="changeSorter('weight_desc')"
        >推荐</a
      >
      <a
        href="javascript:;"
        :class="{ active: sorter === null }"
        @click="changeSorter(null)"
        >最新</a
      >
      <div class="logo">
        <img src="http://weldon.net.cn/mj-m/img/logo.dbd40807.png" alt="" />
      </div>
    </nav>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <ArticleItem
        v-for="article in list"
        :key="article.id"
        :article="article"
      />
    </van-list>
  </div>
</template>

<script>
import { getArticleList } from '@/api/article'

export default {
  name: 'article-page',
  data() {
    return {
      list: [],
      finished: false,
      loading: false,
      current: 1,
      sorter: 'weight_desc',
    }
  },
  methods: {
    async onLoad() {
      const { data } = await getArticleList({
        current: this.current,
        sorter: this.sorter,
      })
      this.list = this.list.concat(data.rows)
      this.loading = false
      this.current++

      //所有文章加载完毕，将finished设为true
      if (this.current > data.pageTotal) this.finished = true
    },

    changeSorter(type) {
      this.sorter = type
      this.list = []
      this.finished = false
      this.loading = true
      this.current = 1
      this.onLoad()
    },
  },
}
</script>

<style lang="less" scoped>
.article-page {
  margin-bottom: 50px;
  margin-top: 44px;
  .my-nav {
    height: 44px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
    background: #fff;
    display: flex;
    align-items: center;
    > a {
      color: #999;
      font-size: 14px;
      line-height: 44px;
      margin-left: 20px;
      position: relative;
      transition: all 0.3s;
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: 0;
        height: 2px;
        background: #222;
        transition: all 0.3s;
      }
      &.active {
        color: #222;
        &::after {
          width: 14px;
        }
      }
    }
    .logo {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      > img {
        width: 64px;
        height: 28px;
        display: block;
        margin-right: 10px;
      }
    }
  }
}
</style>
